.input {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.checkbox {
  display: inline-block;
  height: 20px;
  width: 20px;
  min-width: 20px;
  background: #fff;
  border: 2px #ddd solid;
  margin-right: 4px;
}

.label {
  font-size: 26px;
  display: flex;
  align-items: flex-start;
  gap: 10px;
  color: #fff;
  text-transform: capitalize;
  cursor: pointer;
}
